@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap");

*{
    font-family: "Poppins", sans-serif;
    transition: all 0.25 ease;
}

@font-face {
    font-family: neon;
    src: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/707108/neon.ttf);
  }

html,
body,
header,
main,
footer,

 #intro { 
    background:url("./bg_main1.gif") no-repeat center center fixed #000000;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    /* -webkit-animation: bgcolor 8s infinite;
    animation: bgcolor 16s infinite; */
}

@keyframes bgcolor {
    0% {
        background-color: #45a3e5
    }

    30% {
        background-color: #66bf39
    }

    60% {
        background-color: #eb670f
    }

    90% {
        background-color: #f35
    }

    100% {
        background-color: #864cbf
    }  
}
@keyframes bd{
    0% {
        box-shadow:
        0 0 25px 5px rgba(255, 255, 255, 0.418),  /* inner white */
        0 0 45px 10px rgba(255, 0, 255, 0.418), /* middle magenta */
        0 0 65px 12px rgba(0, 255, 255, 0.418);
    }

    30% {
        
        box-shadow:
        0 0 25px 10px rgba(255, 255, 255, 7),  /* inner white */
        0 0 45px 20px rgba(255, 0, 255, 0.7), /* middle magenta */
        0 0 65px 25px rgba(0, 255, 255, 0.7);
    }

    60% {
        
        box-shadow:
        0 0 25px 10px rgba(255, 255, 255, 8),  
        0 0 45px 25px rgba(255, 0, 255, 8), 
        0 0 65px 30px rgba(0, 255, 255, 8);
    }

    90% {
        
        box-shadow:
        0 0 25px 10px rgba(255, 255, 255, 7), 
        0 0 45px 20px rgba(255, 0, 255, 0.7), 
        0 0 65px 25px rgba(0, 255, 255, 0.7);
    }

    100% {
        
        box-shadow:
        0 0 25px 10px rgba(255, 255, 255, 0.418), 
        0 0 45px 25px rgba(255, 0, 255, 0.418), 
        0 0 65px 40px rgba(0, 255, 255, 0.418);
    }
}
.container{
    margin: 0vh;
    padding: 0vh;
    align-self:center
}
.box_left{
    animation:1s ease-in;
    animation-duration: 0.5s;
}
.box_right{
    animation:1s ease-in;
    animation-duration: 1s;
}
.box-info1{
    margin:0;
    padding-left: 30px;
    padding-right: 30px;
    padding-top: 10px;
    padding-bottom: 10px;
    height: auto;
    width: auto;
    background-image: linear-gradient(to bottom, rgba(111, 0, 255, 0.7), rgba(211, 2, 218, 0.747));
    color: white;
    -webkit-animation: bd 2s infinite;
    animation: bd 2s infinite;
    border-radius: 5px;
    transition: all 0.25s ease;
    cursor: pointer;
}

.box-info1:hover{
    transform: scale(1.05);
}

.text-info1{
    text-shadow: 8px 8px 10px rgba(0, 0, 0, 0.678);
}

#pic-person{
    height: 260px;
    width: 260px;
    border-radius: 50%;
    border: solid 2px rgb(0, 0, 0);
    box-shadow: 0px 0px 15px 5px rgba(0, 0, 0, 0.623);
    border-collapse: collapse;
}
.line1{
    -webkit-animation: bgcolor 1s infinite;
    animation: bgcolor 1.2s infinite;
}
.box-info2{
    background-image: linear-gradient(to bottom, rgba(111, 0, 255, 0.7), rgba(211, 2, 218, 0.747));
    padding-left: 30px;
    padding-right: 30px;
    border-radius: 5px;
    left: -50px;
    color: white;
    -webkit-animation: bd 2s .5s infinite;
    animation: bd 3s infinite;
    transition: all .25s ease;
    cursor: pointer;
}

.box-info2:hover{
    transform: scale(1.05);
}

.textrun{
    animation:1s ease-in;
    animation-duration: 1.5s;
}
.neon {
    font-family: neon;
    color: #FB4264;
    font-size: 9vw;
    /* line-height: 9vw; */
    text-shadow: 0 0 3vw #F40A35;
  }
  .neon {
    animation: neon 1s ease infinite;
    -moz-animation: neon 1s ease infinite;
    -webkit-animation: neon 1s ease infinite;
  }

  @keyframes neon {
    0%,
    100% {
      text-shadow: 0 0 1vw #FA1C16, 0 0 3vw #FA1C16, 0 0 10vw #FA1C16, 0 0 10vw #FA1C16, 0 0 .4vw #FED128, .3vw .3vw .1vw #806914;
      color: #FED128;
    }
    50% {
      text-shadow: 0 0 .3vw #800E0B, 0 0 1.5vw #800E0B, 0 0 5vw #800E0B, 0 0 5vw #800E0B, 0 0 .2vw #800E0B, .3vw .5vw .1vw #40340A;
      color: #806914;
    }
  }

  .button {
    position: relative;
    display: block;
    padding:  5px;
    text-transform: uppercase;
    letter-spacing: 4px;
    text-decoration: none;
    font-size: 24px;
    overflow: hidden;
    transition: 0.2s;
    font-family: neon;
    border-radius: 5px;
}



.button:hover {
    color: #255784;
    background: #2195f300;
    box-shadow: 0 0 5px #806914, 0 0 20px #FED128, 0 0 40px #FA1C16;
    transition-delay: 1s;
}


.button span {
    position: absolute;
    display: block;
}




.button span:nth-child(1) {
    top: 0;
    left: -100%;
    width: 100%;
    height: 2px;
    background: linear-gradient(90deg, transparent, #FED128);
}



.button:hover span:nth-child(1) {
    left: 100%;
    transition: 1s;
}


.button span:nth-child(3) {
    bottom: 0;
    right: -100%;
    width: 100%;
    height: 2px;
    background: linear-gradient(270deg, transparent, #FED128);
}

.button:hover span:nth-child(3) {
    right: 100%;
    transition: 1s;
    transition-delay: 0.5s;
}



.button span:nth-child(2) {
    top: -100%;
    right: 0;
    width: 2px;
    height: 100%;
    background: linear-gradient(180deg, transparent, #FED128);
}

.button:hover span:nth-child(2) {
    top: 100%;
    transition: 1s;
    transition-delay: 0.25s;
}



.button span:nth-child(4) {
    bottom: -100%;
    left: 0;
    width: 2px;
    height: 100%;
    background: linear-gradient(360deg, transparent, #FED128);
}

.button:hover span:nth-child(4) {
    bottom: 100%;
    transition: 1s;
    transition-delay: 0.75s;
}

.group-of-button{
    display:flex;
    justify-content: space-between;
}

.modal-content{
    border-radius: 10px;
}